.header {
  z-index: 2;
  border: 1.5px solid var(--text-color);
  background-color: var(--body-background-color);
  border-radius: 0 0 8px 8px;
  align-items: center;
  padding-top: 18px;
  padding-bottom: 18px;
  display: flex;
  position: sticky;
  top: 0;
}

.header-title {
  display: flex;
}

.header-logo-name {
  color: #000;
}

.title-link {
  letter-spacing: -.03em;
  color: var(--text-secondary-color);
  align-items: center;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.29;
  text-decoration: none;
  display: flex;
}

.header-link-home {
  text-transform: uppercase;
  letter-spacing: -.04em;
  color: var(--text-secondary-color);
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
}

.current {
  background: #eac645;
  border-radius: 36px;
  padding: 8px 16px;
  font-weight: 700;
}

.header-shopping-link {
  letter-spacing: -.04em;
  color: var(--text-secondary-color);
  fill: var(--text-color);
  align-items: center;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
  text-decoration: none;
  display: flex;
}

.header-shopping-link.current {
  color: #111;
  fill: #111;
  background: #eac645;
  border-radius: 36px;
  padding: 7px 16px;
  font-weight: 700;
}

.header-shopping {
  text-transform: uppercase;
}

.header-nav {
  gap: 24px;
  display: flex;
}

.shopping-icon {
  fill: #111;
  width: 20px;
  height: 20px;
  position: relative;
}

.header-icon {
  fill: var(--text-fourth-color);
  width: 109px;
  height: 26px;
  position: relative;
}

.theme-switch {
  width: 40px;
  height: 20px;
  margin-left: auto;
  position: relative;
}

.theme-switch input {
  display: none;
}

.theme-switch .toggle {
  cursor: pointer;
  background: linear-gradient(#4f2ee8, #dcdcdc);
  border-radius: 20px;
  width: 40px;
  height: 20px;
  transition: background-color .3s;
  position: absolute;
  top: 0;
  left: 0;
}

.theme-switch .toggle:before {
  content: "";
  background-color: #ffffe8;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  transition: transform .3s;
  position: absolute;
  top: 1px;
  left: 1px;
}

.theme-switch input:checked + .toggle {
  background: linear-gradient(#4f2ee8, #686868);
}

.theme-switch input:checked + .toggle:before {
  transform: translateX(20px);
}

