.scroll-up-btn-icon {
  cursor: pointer;
  fill: var(--body-secondary-color);
  stroke-width: 1px;
  background: radial-gradient(183.6% 183.01% at 59.76% -23.42%, var(--text-fourth-color) 18.03%, var(--body-secondary-color) 100%);
  border-radius: 50%;
  transition: transform .25s linear, stroke .25s linear;
  animation: 8s infinite pulse-animation;
}

@keyframes pulse-animation {
  0% {
    transform: scale(.9);
  }

  25% {
    transform: scale(1.1);
  }

  35% {
    transform: scale(.9);
  }

  50% {
    transform: scale(1);
  }

  65% {
    transform: scale(.9);
  }

  75% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(.9);
  }
}

.js-scroll-up .scroll-up-btn-icon:hover, .js-scroll-up .scroll-up-btn-icon:focus {
  fill: rgba(79, 46, 232, .7);
  animation-play-state: paused;
}

.js-scroll-up {
  opacity: 1;
  transition: opacity .25s linear;
}

.js-scroll-up_hidden {
  opacity: 0;
}

